.App {
  font-family: sans-serif;
  text-align: center;
}

.input_container,
.label_container,
.button_container,
.table-container {
  display: flex;
  justify-content: space-around;
}

.table_container {
  margin: 10px 0px;
}

.input_container {
  padding: 20px 0px;
}

.title_header {
  font-size: 2.5rem;
  font-weight: bold;
  text-transform: uppercase;
  /* color: #fff; */
}

.body_container {
  margin: 30px 0px;
}

.error {
  /* color: #fefefe; */
  color: red;
  font-weight: bold;
}

